.root {
  backdrop-filter: var(--bgEffect);
}

.message {
  background: var(--weekendBg);
  color: var(--weekendColor);
  border: 2px solid var(--weekendBorderColor);
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  font-weight: bold;
  font-size: 1.3rem;
  height: 40vh;
}

